<template>
  <div id="orderDetail" v-if="orderInfo">
    <!-- 订单的详细信息 -->
    <!-- 顶部提示 -->
    <div class="orderDetail_top">
      <div class="orderDetail_t_l">
        <van-image fit="cover" :src="orderInfo.status_pic" />
      </div>
      <div class="orderDetail_t_r">
        <p>{{ orderState._msg }}</p>
        <p>{{ orderInfo._add_time }}</p>
      </div>
    </div>
    <!-- 进度 -->
    <div class="order_tempo">
      <van-steps
        :active="active"
        active-color="#fc4141"
        inactive-color="#282828"
      >
        <van-step v-for="(i, index) in arr" :key="index">
          <span>{{ i }}</span>
          <template v-slot:inactive-icon>
            <i class="iconfont icon-yuandian un"></i>
          </template>
          <template v-slot:active-icon>
            <i class="iconfont icon-radio-off-full now"></i>
          </template>
        </van-step>
      </van-steps>
    </div>
    <!-- 地址 -->
    <div class="order_address">
      <p>
        <span>{{ orderInfo.real_name }}</span
        ><span>{{ orderInfo.user_phone }}</span>
      </p>
      <p>
        <span>{{ orderInfo.user_address }}</span>
      </p>
    </div>
    <!-- 线 -->
    <div class="address_line">
      <img src="http://47.115.51.185/h5/img/line.05bf1c84.jpg" alt="" />
    </div>
    <!-- 商品 -->
    <div class="orderDetail_googs">
      <div class="orderDetail_googs_num">共1件商品</div>
      <div class="orderDetail_googs_g"><Psd :i="googsInfo" red="r"></Psd></div>
      <!-- 联系客服 -->
      <div class="qq">
        <p>
          <span><van-icon name="service-o" /></span>联系客服
        </p>
      </div>
    </div>
    <!-- 订单信息 -->
    <div class="orderDetail_info">
      <div>
        <p>订单编号</p>
        <p>{{ orderInfo.order_id }}<span @click="copy">复制</span></p>
      </div>
      <div>
        <p>下单时间</p>
        <p>{{ orderInfo._add_time }}</p>
      </div>
      <div>
        <p>订单类型：</p>
        <p>普通订单</p>
      </div>
      <div>
        <p>支付状态：</p>
        <p>{{ orderState._title }}</p>
      </div>
      <div>
        <p>支付方式：</p>
        <p>{{ orderState._payType }}</p>
      </div>
    </div>
    <!-- 金额信息 -->
    <div class="orderDetail_price">
      <p class="pay_price item">
        支付金额：<span>￥{{ orderInfo.total_price }}</span>
      </p>
      <p class="integral item" v-if="orderInfo.deduction_price != 0.0">
        积分抵扣：<span>-￥{{ orderInfo.deduction_price }}</span>
      </p>
      <p class="freight item" v-if="orderInfo.pay_postage != 0.0">
        运费：<span>￥{{ orderInfo.pay_postage }}</span>
      </p>
      <p class="actual">
        实付款：<span>￥{{ orderInfo.pay_price }}</span>
      </p>
    </div>
    <!-- 底部按钮 -->
    <!-- 未支付成功 -->
    <div class="orderDetail_btn" v-if="result == 0">
      <div class="btn cancel" @click="cancel">取消订单</div>
      <div class="btn red" @click="pay">立即付款</div>
    </div>
    <!-- 支付成功 -->
    <div class="orderDetail_btn" v-if="result == 1">
      <div class="btn refund">申请退款</div>
    </div>
    <!-- 点击立即付款的面板 -->
    <van-action-sheet v-model="show" title="选择付款方式">
      <Pay :keys="orderInfo.order_id"></Pay>
    </van-action-sheet>
  </div>
</template>
<script>
// 商品
import Psd from "../components/OrderRefund/OrderGoodsPsd.vue";
// 支付
import Pay from "../components/OrderStatus/Pay.vue";
export default {
  components: {
    Psd,
    Pay,
  },
  data() {
    return {
      // 订单信息
      orderInfo: null,
      // 订单状态信息
      orderState: null,
      // 商品信息
      googsInfo: null,
      //   进度
      active: 0,
      arr: ["待付款", "待发货", "待收货", "待评价", "已完成"],
      show: false,
      // 余额
      now_price: null,
      // 订单状态 0表示未支付，1表示支付成功
      result: null,
    };
  },
  created() {
    let id = this.$route.params.id;
    this.axios.get("/api/order/detail/" + id).then((res) => {
      let data = res.data.data;
      // console.log(data);
      this.orderInfo = data;
      this.orderState = data._status;
      this.googsInfo = data.cartInfo[0];
      this.result = data._status._type;
      this.active = this.result;
    });
  },
  methods: {
    // 取消订单
    // 先弹出确认框
    // 确认取消之后跳转到订单信息页面
    cancel() {
      this.$dialog
        .confirm({
          title: "提示",
          message: "确认取消该订单",
        })
        .then(() => {
          this.axios
            .post("/api/order/cancel", {
              id: this.orderInfo.order_id,
            })
            .then(() => {
              this.$router.push("/order/list/0");
            });
        })
        .catch(() => {});
    },
    // 立即付款
    pay() {
      this.show = true;
    },
    // 复制按钮
    copy() {
      this.$toast({
        type: "success",
        message: "复制成功",
        duration: "400",
      });
    },
  },
};
</script>
<style lang="less">
#orderDetail {
  padding-bottom: 1.4rem;
  // 顶部开始
  .orderDetail_top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    height: 2.4rem;
    padding: 0 0.3rem;
    background-color: #e93323 !important;
    .orderDetail_t_l {
      width: 1.8rem;
      height: 1.8rem;
      overflow: hidden;
    }
    .orderDetail_t_r {
      flex: 1;
      text-align: left;
      color: hsla(0, 0%, 100%, 0.8);
      font-size: 0.26rem;
      margin-left: 0.28rem;
      p:nth-child(1) {
        font-size: 0.34rem;
        font-weight: 700;
        color: #fff;
        margin-bottom: 0.07rem;
      }
    }
  }
  // 顶部结束
  //   进度
  .order_tempo {
    //   外
    background-color: #fff;
    box-sizing: border-box;
    padding: 0 0.5rem;
    .van-steps--horizontal {
      .van-steps__items {
        // 已完成
        .van-step--finish {
          color: #fc4141 !important;
          .van-step__title {
            color: #282828;
          }
        }
        // 每个
        .van-hairline {
          text-align: left;
          // 文字
          .van-step__title {
            //   transform: translateX(-30%) !important;
            text-align: left;
          }
          .icon-radio-off-full {
            color: #fc4141;
            font-size: 0.08rem;
          }
        }
        // 第一个和最后一个的文字居中
        .van-hairline:first-child {
          .van-step__title {
            transform: translateX(-27%);
            span {
            }
          }
        }
        .van-hairline:last-child {
          .van-step__title {
            transform: translateX(27%) !important;
          }
        }
      }
    }
  }
  //   地址
  .order_address {
    text-align: left;
    font-size: 0.26rem;
    color: #868686;
    background-color: #fff;
    padding: 0.25rem 0.3rem 0.3rem 0.3rem;
    p:nth-child(1) {
      display: flex;
      align-items: center;
      font-size: 0.3rem;
      color: #282828;
      margin-bottom: 0.1rem;
      span:nth-child(1) {
        margin-right: 0.3rem;
      }
    }
  }
  //   线
  .address_line {
    width: 100%;
    height: 0.03rem;
    position: relative;
    img {
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  //   商品
  .orderDetail_googs {
    background-color: #fff;
    box-sizing: border-box;
    .orderDetail_googs_num {
      height: 0.86rem;
      padding: 0 0.3rem;
      margin: 0.2rem 0;
      border-bottom: 0.01rem solid #eee;
      font-size: 0.38rem;
      color: #282828;
      line-height: 0.86rem;
      text-align: left;
    }
    .orderDetail_googs_g {
      box-sizing: border-box;
      padding: 0 0.3rem;
      margin: 0.3rem 0;
    }
    .qq {
      color: #e93323;
      box-sizing: border-box;
      height: 1.2rem;
      //   padding-left: 0.3rem;
      padding: 0.3rem 0 0.3rem 0.3rem;
      border-bottom: 0.01rem solid #eee;
      font-size: 0.42rem;
      line-height: 1.2rem;
      background: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      span {
        margin-right: 0.15rem;
        font-size: 0.4rem;
      }
      p {
        width: 100%;
        border-top: 0.01rem solid #eee;
      }
    }
  }
  //   订单信息
  .orderDetail_info {
    background-color: #fff;
    margin-top: 0.12rem;
    padding: 0.3rem;
    div {
      font-size: 0.28rem;
      color: #282828;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 0.34rem 0;
      p:nth-child(2) {
        color: #868686;
        text-align: right;
        span {
          font-size: 0.2rem;
          color: #333;
          border-radius: 0.03rem;
          border: 1px solid #666;
          padding: 0.03rem 0.15rem;
          margin-left: 0.24rem;
        }
      }
    }
  }
  // 金额
  .orderDetail_price {
    box-sizing: border-box;
    padding: 0.3rem;
    background-color: #fff;
    margin-top: 0.12rem;
    font-size: 0.28rem;
    .item {
      text-align: left;
      display: flex;
      align-items: center;
      font-size: 0.28rem;
      color: #282828;
      justify-content: space-between;
      margin: 0.18rem 0;
      span {
        color: #868686;
      }
    }
    .actual {
      border-top: 0.01rem solid #eee;
      margin-top: 0.3rem;
      padding-top: 0.3rem;
      text-align: right;
      span {
        font-weight: 700;
        color: #fc4141;
        font-size: 0.3rem;
      }
    }
  }
  // 底部
  .orderDetail_btn {
    width: 100vw;
    height: 1.2rem;
    position: fixed;
    bottom: 0;
    left: 0;
    background-color: #fff;
    padding: 0 0.3rem;
    border-top: 1px solid #eee;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .btn {
      width: auto;
      height: 0.6rem;
      text-align: center;
      border-radius: 0.5rem;
      color: #fff;
      font-size: 0.27rem;
      padding: 0.4rem 0.5rem;
      display: flex;
      align-items: center;
    }
    .cancel {
      color: #aaa;
      border: 1px solid #ddd;
    }
    .red {
      background-color: #e93323 !important;
      margin-left: 0.18rem;
    }
    // 退款
    .refund {
      color: #aaa;
      border: 1px solid #ddd;
    }
  }
  // 付款方式
  .van-popup {
    .van-action-sheet__header {
      height: 1.44rem;
      font-size: 0.48rem;
      color: #282828;
      font-weight: 700;
    }
  }
}
</style>